<template>
    <div>
        <navbar title="个人资金"  v-if="$phonemodel == 'Android'"></navbar>
        <div class="app" :class="{nav:$phonemodel == 'Android'}">
            <div class="myHead pr ov">
                <div class="imgBox">
                    <img src="@/static/images/my/usdt_icon@2x.png" alt="">
                    <span style="color: #fff;">USDT</span>
                    <i >{{walletData.money}} USDT</i>
                </div>
                <div class="textBox">
                    <p style="width:2.4rem; overflow:hidden;text-overflow:ellipsis;white-space:nowrap;">{{walletData.address}}</p>
                    <span class="copy" @click="copy(walletData.address)"></span>
                    <i @click="codeshow = !codeshow"></i>
                </div>
                <p class="atext">注释:本地址只接受erc20版本的usdt，请勿充值其它币种，否则无法找回</p>
            </div>
            <ul class="myList">
                <!--<li class="active pr"><span></span><a href="capital.html">充值</a></li>-->
                <li class="pr" :class="{active:hotList == 1}" @click="hotList = 1"><span></span><a href="javascript:void (0)">提现记录</a></li>

                <li class="pr" :class="{active:hotList == 2}" @click="hotList = 2"><span></span><a href="javascript:void (0)">内部转账记录</a></li>
            </ul>
            <cashlist v-show="hotList == 1"></cashlist>
            <transferlist v-show="hotList == 2"></transferlist>
            <div class="btn ov">
                <router-link class="fl" to="/withdraw">提现</router-link>
                <router-link class="fr" to="/transfer">内部转账</router-link>
            </div>
        </div>

        <van-dialog
                v-model="codeshow"
                title="充值二维码"
                show-cancel-button
                :showCancelButton="false"
                style="text-align: center"
        >
            <img :src="walletData.address_qrcode" >
        </van-dialog>
    </div>
</template>

<script>
    import {wallet, withdrawInfo, withdraw, transfer} from "../../api/center";
    import fmath from "../../utils/math"
    import { toast, Dialog } from 'vant';
    import Clipboard from 'clipboard'
    import navbar from "@/components/navbar";
    import {cashlist, transferlist} from "@/page/mine/components";

    export default {
        name: "Wallet",
        components:{
          navbar,
            [Dialog.Component.name]: Dialog.Component,
            cashlist,
            transferlist
        },
        data()
        {
            return {
                codeshow:false,
                walletData:{},
                hotList:1,
            }
        },
        mounted()
        {
            this.getWallet()
        },
        methods:{
            getWallet()
            {
                wallet().then(res => {
                    this.walletData = res
                })
            },

            // 复制邀请码
            copy(copyText)
            {

                var clipboard = new Clipboard('.copy', {
                    text: function() {
                        return copyText;
                    }
                });
                clipboard.on('success', e => {

                    toast('复制成功')
                    // 释放内存
                    clipboard.destroy()
                })
                clipboard.on('error', e => {
                    toast('复制失败')
                    // 释放内存
                    clipboard.destroy()
                })

            },
        }
    }
</script>

<style scoped>
        @import "../../static/css/wallet.css";
</style>



